<script>
import BarryGbImgUrl from './imgUrl';
import { ValidationProvider, ValidationObserver, extend } from 'vee-validate';
import { alpha, numeric, length, required } from 'vee-validate/dist/rules';
const axios = require('axios');
const qs = require('qs');
const moment = require('moment');

extend('userName', value => {
  let regex = /^[가-힣|aA-zZ]*$/.test(value);
  if (!regex) {
    return '한글 또는 영문 입력만 가능합니다.';
  }
  else {
    return true;
  }
});
extend('alpha', {
  ...alpha,
  message: '문자 입력만 가능합니다.'
});
extend('numeric', {
  ...numeric,
  message: '숫자 입력만 가능합니다.'
});
extend('length', {
  ...length,
  message: '최소 입력이 필요합니다.'
});
extend('required', {
  ...required,
  message: '필수 입력 항목 입니다.'
});

export default {
  name: 'couponSelect',
  components:{
    BarryGbImgUrl,
    ValidationProvider,
    ValidationObserver
  },
  data () {
    return {
      msg: 'coupon msg',
      returnData: false,
      thisYear: moment().format('YY'),
      dismissSecs: 10,
      dismissCountDown: 0,
      selectData:{
        paymentType:false,
        couponId:false,
        couponSubject:false,
        couponType:false,
        couponTimeleft:0,
        amount: 0,
        couponMsg:false,
      },
      //추 후 DB 데이터 가져오기...
      couponList:[
        {id:4,title:'test 쿠폰',type:'seller',timeleft:'100',price:'1000',msg:'TEST 100일권 입니다.'},
        {id:1,title:'판매자 30일 쿠폰',type:'seller',timeleft:'30',price:'30000',msg:'판매자 30일 쿠폰 입니다.'},
        {id:2,title:'판매자 365일 쿠폰',type:'seller',timeleft:'365',price:'300000',msg:'판매자 365일 쿠폰 입니다.'},
        {id:3,title:'프리미엄 쿠폰',type:'premium',timeleft:'31',price:'50000',msg:'31일간 프리미엄 혜택(카드 배너 광고, 상위 노출)이 주어집니다.'},
      ],
      couponPaymentType:[
        {title:'신용카드',type:'creditCard', active:false},
        {title:'가상계좌',type:'virtualBank', active:false},
      ],
      virtualBankInfo:{
        bankCodeOptions: [
          { value: 'BK03', text: '기업은행' },
          { value: 'BK04', text: '국민은행' },
          { value: 'BK20', text: '우리은행' },
          { value: 'BK07', text: '수협' },
          { value: 'BK11', text: '농협' },
          { value: 'BK32', text: '부산은행' },
          { value: 'BK23', text: 'SC제일은행'},
          { value: 'BK26', text: '신한은행'},
          { value: 'BK81', text: 'KEB하나은행'},
          { value: 'BK34', text: '광주은행'},
          { value: 'BK71', text: '우체국'},
          { value: 'BK31', text: '대구은행'},
          { value: 'BK39', text: '경남은행'},
        ],
        cashReceiptUseOptions: [
          { value: 0, text: '미발급'},
          { value: 1, text: '발급'},
        ],
        cashReceiptTypeOptions: [
          { value: 0, text: '소득공제용'},
          { value: 1, text: '지출증빙용'},
        ],
      },
      creditCardForm: {
        cardNumber: '',
        expireMonth: '',
        expireYear: '',
        birthday: '',
        userName: '',
        userMobileNumber: '',
        cardPw: '',
        quota: '0',
      },
      virtualBankForm: {
        bankCodeSelected: '',
        cashReceiptUseSelected: 0,
        cashReceiptTypeSelected: '',
        cashReceiptNo: '',
        userMobileNumber: '',
      },
      form: {

      },
      submitBtnStatus: false
    }
  },
  filters: {
    currency(value) {
      var num = new Number(value);
      return num.toFixed(0).replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1,")
    }
  },
  methods:{
      showModal(index) {
        this.$refs['paymentType'].show();
        this.selectData.couponId = this.couponList[index].id;
        this.selectData.couponSubject = this.couponList[index].title;
        this.selectData.couponType = this.couponList[index].type;
        this.selectData.couponTimeleft = this.couponList[index].timeleft;
        this.selectData.amount = this.couponList[index].price;
        this.selectData.couponMsg = this.couponList[index].msg;
      },
      hideModal() {
        this.$refs['paymentType'].hide()
      },
      activeFilter(index) {
        let couponPaymentTypeCount = (this.couponPaymentType.length)-1;
        for(let i=0;i<=couponPaymentTypeCount;i++){
          this.couponPaymentType[i].active = false;
        }
        this.couponPaymentType[index].active = !this.couponPaymentType[index].active;
        this.selectData.paymentType = this.couponPaymentType[index].type;
      },
      async onSubmit(event) {
        event.preventDefault();
        let confirm = false;
        const valid = (this.selectData.paymentType == 'creditCard') ? await this.$refs.creditCardObserver.validate() : await this.$refs.virtualBankObserver.validate();

        if(valid === false){
          return false;
        }

        //각 form 데이터들 한개 obj 에 넣고 for문 1회만.. deepmerge..
        if(this.selectData.paymentType == 'creditCard'){
          this.form = {...this.selectData, ...this.creditCardForm};
          confirm = true;
        }
        else{
          let message = {
            title: '정말 가상 계좌를 발급 하시겠습니까?',
            body: '가상 계좌 발급 시 3일 후 새로운 가상 계좌를 발급 할 수 있습니다. 단 가상 계좌 결제를 완료 한 경우라면 다시 가상 계좌 발급이 가능합니다.'
          }

          let options = {
            okText: '네',
            cancelText: '아니오',
          };
          await this.$dialog
            .confirm(message,options)
            .then((dialog) => {
              this.form = {...this.selectData, ...this.virtualBankForm};
              confirm = true;
            })
            .catch(() => {
              confirm = false;
            });
        }

        if(confirm === false){
          return false
        }

        const vm = this;
        let formSendData = qs.stringify(this.form);
        vm.submitBtnStatus = true;
        //axios.post('http://local_barry/API/coupon/payment',
        axios.post('https://barrybarries.kr/API/coupon/payment',
            formSendData,
  {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
            }
        })
        .then((response) => {
          //couponCode, couponMsg
          console.log(response);
          //가상 계좌 성공인 경우 리턴 data가 존재함.
          if(response.data.data){
            vm.returnData = response.data.data;
            vm.submitBtnStatus = true;
          }
          else{
            vm.submitBtnStatus = false;
          }
          this.showAlert();
          vm.msg = response.data.couponMsg;

        })
        .catch((error) => {
          this.showAlert();
          vm.msg = 'API 서버 오류 입니다. 관리자에 문의해주세요.';
          vm.submitBtnStatus = false;
        });

      },
      onReset(event) {
        event.preventDefault()
        for(let key in this.creditCardForm){
          this.creditCardForm[key] = '';
        }
        for(let key in this.virtualBankForm){
          this.virtualBankForm[key] = '';
        }
        this.$nextTick(() => {

        })
      },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    }
  }
}
</script>

