<script src="../main.js"></script>
<script src="../../config/index.js"></script>
<template>
    <img :src="imgMobileUrlPrefix + inputImgUrl" v-bind="$attrs">
</template>


<script>
export default {
  name: 'barry-gb-img-url',
  props: {
    inputImgUrl: {
      type: String,
      required: false,
      defalut: 'notImg',
    },
  },
  data () {
    return {
      imgMobileUrlPrefix: 'https://barrybarries.kr/theme/barryMall_01/mobile/img',
    }
  }
}
</script>



