<script src="../main.js"></script>
<script src="../../config/index.js"></script>
<template>
  <article id="coupon" class="contentsWrap">
    <div class="contents container-fluid">
        <section class="info">
          <div class="title">
            <h3>구매 쿠폰 목록</h3>
          </div>
          <b-table
            show-empty
            small
            striped
            stacked="md"
            :items="myCouponlist"
            :fields="fields"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :current-page="currentPage"
            :per-page="perPage"
            detailsShowing="true"
            :empty-text="'구매한 쿠폰이 없습니다.'"
          >
            <template #cell(status) = "row">
              <template v-if="row.item.status == 'deactivation'">
                <b-badge variant="danger">만료된 쿠폰</b-badge>
              </template>
              <template v-else>
                <b-badge variant="success">유효한 쿠폰</b-badge>
              </template>
            </template>

            <template #cell(price) = "row">
              {{ row.item.price | currency}} 원
            </template>

            <template #cell(timeleft) = "row">
              {{ row.item.timeleft }} 일
            </template>

            <template #cell(유효기간) = "row">
              <template v-if="row.item.status == 'deactivation'">
                <b-badge variant="danger">활성화 된 쿠폰이 아닙니다.</b-badge>
              </template>
              <template v-else>
                <p>{{ row.item.activationDatetime }}</p> ~ <p>{{ row.item.deactivationDatetime }}</p>
              </template>
            </template>

            <template #row-details="row">
              <b-table-simple>
                <b-tbody>
                  <b-tr v-if="row.item.paymentType == 'virtualBank'">
                    <b-th>
                      가상 계좌 예금주
                    </b-th>
                    <b-td>
                      {{row.item.bankHolder}}
                    </b-td>
                  </b-tr>
                  <b-tr v-if="row.item.paymentType == 'virtualBank'">
                    <b-th>
                      은행명
                   </b-th>
                    <b-td>
                      {{row.item.bankName}}
                    </b-td>
                  </b-tr>
                  <b-tr v-if="row.item.paymentType == 'creditCard'">
                    <b-th>
                      승인 번호
                    </b-th>
                    <b-td>
                      {{row.item.cardAuthNumber}}
                    </b-td>
                  </b-tr>
                  <b-tr v-if="row.item.paymentType == 'virtualBank'">
                    <b-th>
                      가상 계좌 번호
                    </b-th>
                    <b-td>
                      {{row.item.bankAccount}}
                    </b-td>
                  </b-tr>
                  <b-tr v-if="row.item.paymentType == 'virtualBank'">
                    <b-th>
                      가상 계좌 발급일
                    </b-th>
                    <b-td>
                      {{row.item.bankActivationDatetime}}
                    </b-td>
                  </b-tr>
                  <b-tr v-if="row.item.paymentType == 'virtualBank'">
                    <b-th>
                      가상 계좌 입금 만료일
                    </b-th>
                    <b-td>
                      {{row.item.bankExpireDatetime}}
                    </b-td>
                  </b-tr>
                  <b-tr>
                    <b-th>
                      결제 수단
                    </b-th>
                    <b-td v-if="row.item.paymentType == 'creditCard'">
                      <b-badge>카드</b-badge>
                    </b-td>
                    <b-td v-else-if="row.item.paymentType == 'virtualBank'">
                      <b-badge>가상계좌</b-badge>
                    </b-td>
                  </b-tr>
                  <b-tr>
                    <b-th>
                      결제 상태
                    </b-th>
                    <b-td v-if="row.item.paymentStatus == 'wait'">
                      <b-badge variant="warning">결제 대기</b-badge>
                    </b-td>
                    <b-td v-else-if="row.item.paymentStatus == 'fail'">
                      <b-badge variant="danger">결제 실패</b-badge>
                    </b-td>
                    <b-td v-else-if="row.item.paymentStatus == 'cancel'">
                      <b-badge variant="warning">결제 취소</b-badge>
                    </b-td>
                    <b-td v-else-if="row.item.paymentStatus == 'complete'">
                      <b-badge variant="success">결제 완료</b-badge>
                    </b-td>
                  </b-tr>
                  <b-tr v-if="row.item.paymentType == 'creditCard' && row.item.paymentStatus == 'complete' || row.item.paymentStatus == 'cancel'">
                    <b-th>
                      매출 전표
                    </b-th>
                    <b-td>
                      <a :href="'https://cp.payup.co.kr/transaction/popup/receiptInfoSMS.do?trxid='+row.item.transaction" target="_blank">
                        <b-btn
                          variant="info"
                          size="sm"
                        >
                          확인
                        </b-btn>
                      </a>
                    </b-td>
                  </b-tr>
                  <b-tr v-else-if="row.item.paymentType == 'virtualBank' && row.item.paymentStatus == 'complete' || row.item.paymentStatus == 'cancel'">
                    <b-th>
                      입금 전표
                    </b-th>
                    <b-td>
                      <a :href="'https://cp.payup.co.kr/transaction/popup/vaccReceiptInfoSMS.do?trxid='+row.item.transaction" target="_blank">
                        <b-btn
                          variant="info"
                          size="sm"
                        >
                          확인
                        </b-btn>
                      </a>
                    </b-td>
                  </b-tr>
                </b-tbody>
              </b-table-simple>
            </template>

            <template #cell(상세정보) = "row">
              <b-button size="sm" @click="row.toggleDetails">
                {{ row.detailsShowing ? '자세히 보기 취소' : '자세히 보기' }}
              </b-button>
            </template>

            <template #empty="scope">
              <b-alert variant="info" show>
                {{scope.emptyText}}
              </b-alert>
            </template>

          </b-table>
          <b-col sm="12" class="my-1">
            <b-pagination
              v-model="currentPage"
              :total-rows="totalRows"
              :per-page="perPage"
              align="fill"
              size="sm"
              class="my-0"
            >
            </b-pagination>
          </b-col>
        </section>
    </div>
  </article>
</template>

<script>
import BarryGbImgUrl from './imgUrl';
const axios = require('axios');
const qs = require('qs');

export default {
  name: 'couponList',
  components:{
    BarryGbImgUrl
  },
  data () {
    return {
      myCouponlist: [

      ],
      fields: [
        {key: 'uniq', label: '쿠폰 고유 번호', sortable:true},
        {key: 'subject', label: '쿠폰명', sortable:false},
        {key: 'status', label: '쿠폰 상태', sortable:true,},
        {key: 'price', label: '쿠폰 금액', sortable:true,},
        {key: 'timeleft', label: '유효 일', sortable:true,},
        '유효기간',
        {key: 'datetime', label: '쿠폰 생성 일시', sortable:false},
        '상세정보',
      ],
      showDetailsControl:true,
      detailItem : false,
      sortBy: 'uniq',
      sortDesc: true,
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
    }
  },
  filters: {
    currency(value) {
      var num = new Number(value);
      return num.toFixed(0).replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1,")
    }
  },
  created (){
    const vm = this;
    axios.get('https://barrybarries.kr/API/coupon',
    //axios.get('http://local_barry/API/coupon',
      '',
      {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
        }
      })
      .then((response) => {
        console.log(response);
        for(let i=0;i<=response.data.data.couponList.length-1;i++){
          vm.myCouponlist.push(response.data.data.couponList[i]);
        }
        vm.totalRows = response.data.data.couponCount;
        console.log(response.data.data.couponCount);


      })
      .catch((error) => {
        console.log(error);
      });
  },
  mounted (){

  },
  methods:{
    //date time 정렬은 일단 보류,
    dateTimeFormat(value) {
      var d = new Date(value),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear();

      if (month.length < 2)
        month = '0' + month;
      if (day.length < 2)
        day = '0' + day;

      return [year, month, day].join('-');
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



