import Vue from 'vue'
import Router from 'vue-router'
import CouponSelect from '@/components/couponSelect'
import CouponList from '@/components/couponList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component: CouponSelect
    },
    {
      path: '/couponSelect',
      name: 'couponSelect',
      component: CouponSelect
    },
    {
      path: '/couponList',
      name: 'couponList',
      component: CouponList
    }
  ]
})



